
/*++++++++++++++++++++++++++++++ Mobiles/ Cellphones/small screens++++++++++++++++++++++++++++++++++++++++++++++++++++++++++*/
@media screen and (max-width:767px) {
    .header-container{
        width: 100%;
        height: 230px;
        margin: 0 auto;
        
        background-image: url(../../Images/FondoJPG.jpg);
        
        
    }
    .icon {
    
        fill: white;
    }
    /* .icon-search {
    fill
        color: red;
    } */
    .nav-main{
        display: flex;
        justify-content: space-between;
    }
    .logo-name{
        display: flex;
        align-items: baseline;
        margin-top: 48px;
        margin-left: 23px;
    }
    .nav-options{
        margin-top: 48px;
        margin-right: 20px;
        
        
    }
    .list-nav{
        display: flex;
        gap: 39px;
        list-style: none;
        
        
    }
    .list-nav a{
       color: #FFFFFF;
        
    }
    .search-o{
        margin-left: 21px;
        margin-top: 38px;
        display: flex;
        justify-content: center;
    }
    .logo{
        display: none;
        
    }
    .footer-container{
        height: 100px;
        background-color: grey;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .footer-mobile{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        gap: 10px;
        
        
    }
    .fotter-top-messa{
        margin-top: 28.7px;
        margin-bottom: 10px;
    }
    .fotter-top-messa p{
        margin-top: 0px;
        margin-bottom: 0px;
    }
    .footer-bottom p{
        margin-top: 0px;
        margin-bottom: 0px;
    } 
    .nav-underline a::after{
        content: "";
    background-color: #FF6B08;
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
    height: 4px;
    margin-top: 1px;
    
    opacity: 0;
        
    
    } 
    .nav-underline a{
        position: relative;
        text-decoration: none;
    }
     .nav-underline a:hover::after{
        opacity: 1;
    }
    
    .nav-underline a.selected::after{
       
        opacity: 1;
    } 
}

